
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import { phoneGetCode, updatePhone } from '../../service/getData'
import { getStore } from '../../config/mUtils'

export default {
components: {
XHeader,
XInput,
Group,
XButton,
Countdown
},
data () {
return {
title:'绑定手机号码',
sendText:'发送验证码',
time:60,
mobileVal:'',
codeVal:'',
passWordVal:'',
dataCode:'',
isstart:false,
codeDisabled:false,
subDisabled:false
}
},
mounted () {

},
methods:{
finish(){
this.codeDisabled = false
this.isstart = false
},
sendCode(){
if(!this.$refs.mobile.valid){
console.log(this.$refs.mobile)
this.$nextTick(()=>{
this.$vux.toast.show({
text:this.$refs.mobile.errors.format || '请输入手机号码',
width:'10em',
type:'warn'
})
})
}else{
this.time = 60
this.isstart = true
this.codeDisabled = true
phoneGetCode(this.mobileVal).then((res)=>{
if(res.msgCode==='0000'){
console.log(res)
this.dataCode = res.data
this.$nextTick(()=>{
this.$vux.toast.show({
text:'发送成功'
})
})
}
}).catch(()=>{

})
}
},
submit(){
console.log(this.codeVal)
console.log(this.dataCode)
this.subDisabled = true
if(!this.$refs.mobile.valid){
return this.$nextTick(()=>{
this.subDisabled = false
this.$vux.toast.show({
text:this.$refs.mobile.errors.format || '请输入手机号码',
width:'10em',
type:'warn'
})
})
}else if(this.codeVal==='' || this.codeVal!=this.dataCode){
return this.$nextTick(()=>{
this.subDisabled = false
this.$vux.toast.show({
text:this.codeVal===''?'请输入验证码':'' || this.codeVal!=this.dataCode?'验证码错误':'',
width:'10em',
type:'warn'
})
})
}else{
updatePhone(getStore('token'), this.mobileVal).then((res)=>{
console.log(res)
if(res.msgCode==='0000'){
this.$nextTick(()=>{
this.$vux.toast.show({
text:'绑定成功'
})
})
this.subDisabled = false
}else{
this.$nextTick(()=>{
this.$vux.toast.show({
text:'系统繁忙',
type:'cancel'
})
})
this.subDisabled = false
}
})
}
}
}
}



